document.getElementById("btn5").onclick = function (e) {

    new_tag = document.createElement('div');
    new_tag.innerHTML = '';
    document.body.prepend(new_tag);
    let new_tag_class = document.querySelector('div');
    new_tag_class.id += 'new_modal';
    new_tag_class.className += 'center-modal'
    document.querySelector('body').classList.add('overflow-y-hidden');

    let req = getXmlHttp()
    let statusElem = document.getElementById('new_modal')

    req.onreadystatechange = function () {
        if (req.readyState == 4) {
            statusElem.innerHTML = req.statusText
            if (req.status == 200) {
                statusElem.innerHTML = req.responseText;
            }
        }
    }
    req.open('GET', 'modal-status.html', true);
    req.send(null);
    statusElem.innerHTML = ''
}

