window.onclick = function (event) {

    let bg = document.getElementById('bg');
    let bg2 = document.getElementById('bg2');

    if (event.target == bg2) {

        let node = document.querySelector('#bg2');
        node.remove();
        let node1 = document.querySelector('#form1');
        node1.remove();
        let node2 = document.querySelector('#new_modal');
        if (node2 !== null) {
            node2.remove();
        }

    }

    if (event.target == bg) {

        let node = document.querySelector('#bg');
        node.remove();
        let node1 = document.querySelector('#form2');
        node1.remove();
        let node2 = document.querySelector('#new_modal');

        if (node2 !== null) {
            node2.remove();
        }

    }
};




